@import url(https://fonts.google.com/specimen/Josefin+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  box-sizing: border-box;
  font-family: 'Josefin Sans',sans-serif;
  border-spacing: 0;
    padding: 0;
}

:root{
  --primary-color: hsl(220, 98%, 61%);
  --check-background: linear-gradient(to right,hsl(192, 100%, 67%),hsl(280, 87%, 65%));
  --primary-background:hsl(235, 21%, 11%);
  --screen-background: hsl(235, 24%, 19%);
  --primary-font:hsl(234, 39%, 85%);
  --primary-font-hover: hsl(236, 33%, 92%);
  --icons-color : hsl(234, 11%, 52%);
  --icons-weigth-color:hsl(233, 14%, 35%);
  --icons-weigther-color:hsl(237, 14%, 26%);
  font-size: 18px;
}
.task-wrapper{
    display: flex;
    width: 100%;
    align-items: center;
    background-color: var(--screen-background);
    padding: 15px;
    margin-bottom: 1px;
}

.task-select{
    width: 20px;
    height: 20px;
    border: solid 2px var(--icons-weigth-color);
    border-radius: 50%;
    display: flex;
    cursor: pointer;
}

.checked{
    border:0;
    background: linear-gradient(to right,hsl(192, 100%, 67%),hsl(280, 87%, 65%));
}

.task-message{
    margin-left: 10px;
    color: var(--primary-font);
    width: 90%;
}

.completed{
    text-decoration: line-through;
    color: var(--icons-weigth-color);
}
.list-wrapper {
    background-color: var(--icons-color);
    width: 100%;
    border-radius: 3px;
    overflow: hidden;
    margin-top: 20px;
}
.list-wrapper table{
    width: 100%;

}

.row-list{
    width: 100%;
}


.management-wrapper, .management-wrapper *{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.management-wrapper{
    height: 30px;
    background-color: var(--screen-background);
    font-size: 10px;
    color: var(--icons-weigth-color);
    padding: 15px;
}

.filters-wrapper{
    width: 35%;
    justify-content: space-between;
}
.todo-wrapper{
    position: absolute;
    top: 8%;
    width: 400px;
    height: 400px;
}
.todo-banner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--primary-font-hover);
}

.icon-theme{
    cursor: pointer;
}

.input-wrapper{
    width: 100%;
    height: 45px;
    margin-top: 30px;
    background-color: var(--screen-background);
    border-radius: 5px;
    padding: 0 10px;
}

.input-wrapper form{
    height: 100%;
    display: flex;
    align-items: center;
}

.task-input-icon{
    width: 20px;
    height: 20px;
    background: none; 
    border: solid;
    border-color: var(--icons-color);
    border-radius: 50%;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    cursor: pointer;
}

.task-input{
    width: 90%;
    height: 100%;
    background:none;
    border: none;
    outline: none;
    margin-left: 10px;
    color: var(--icons-weigth-color);
    
}
.todo-home{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    height: 100vh;
    width: 100vw;
}

.banner-wrapper{
    height: 30%;
    width: 100%;
}

.banner-img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.bg-default{
    width: 100%;
    height: 70%;
    background-color: var(--primary-background);

}
